@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');
#headBanner {
    background-color: #A35B5B;
    width: 100%;
    height: 10em;
    margin-left: -0.5em;
    margin-top: -0.5em;
    margin-bottom: 1rem;
    padding-right: 1.1em;
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
}

#logo {
    height: 8em;
    padding-left: 2em;
    margin-right: 2em;
}

#title {
    font-size: 3em;
    color: white;
    padding-right: 0.5em;
    font-family: "Ubuntu", "Roboto", "Open Sans", sans-serif;
}

/* version mobile fiche wilder */

@media screen and (max-width:375px) {
    body {
        background: -moz-linear-gradient(top, #FAFFFC 0%, #A45D42 100%, #A35B5B 100%);
        background: -webkit-linear-gradient(top, #FAFFFC 0%, #A45D42 100%, #A35B5B 100%);
        background: linear-gradient(to bottom, #FAFFFC 0%, #A35B5B 100%, #A35B5B 100%);
    }
    #headBanner {
        background-color: #A35B5B;
        width: 100%;
        height: 10em;
        margin-left: -0.5em;
        margin-top: 0.5em;
        margin-bottom: 1rem;
        padding-right: 1.1em;
        display: inline-flex;
        justify-content: space-between;
        align-items: center;
    }
    #logo {
        height: 3em;
        padding-left: 2em;
        margin-right: 2em;
    }
    #title {
        font-size: 1em;
        color: white;
        padding-right: 0.5em;
        text-align: center;
        font-family: "Ubuntu", "Roboto", "Open Sans", sans-serif;
    }
    /*photo des wilder*/
    .ficheWilder, .photoWilder {
        text-align: center;
        animation: apparition 0.8s ease-out;
    }
    .photoWilder {
        height: 100%;
        width: 100%;
        border-radius: 50%;
        align-items: center;
    }
    h2 {
        text-align: center;
        font-family: "Roboto", "Ubuntu", "Open Sans", sans-serif;
        font-weight: normal;
        animation: apparition 0.8s ease-out;
    }
    /* description des wilders */
    .bodyWilder {
        display: block;
        border-radius: 50%;
        background-color: #EDD7B7;
        height: 10rem;
        margin-top: 10%;
        margin-left: 10px;
        margin-right: 10px;
        animation: apparition 0.8s ease-out;
        text-align: center;
        position: absolute;
        background: -moz-linear-gradient(top, #FAFFFC 0%, #A45D42 100%, #A35B5B 100%);
        background: -webkit-linear-gradient(top, #FAFFFC 0%, #A45D42 100%, #A35B5B 100%);
        background: linear-gradient(to bottom, #FAFFFC 0%, #A35B5B 100%, #A35B5B 100%);
    }
    .bodyWilder, p {
        display: flex;
        justify-content: center;
        position: relative;
        font-size: 94%;
        font-family: "Roboto", "Ubuntu", "Open Sans", sans-serif;
        padding-left: 4%;
        padding-right: 4%;
        margin: 0;
    }
    /* logo réseaux sociaux */
    .informations, .socialMedia {
        text-align: center;
        animation: apparition 0.8s ease-out;
    }
    .socialMedia {
        display: inline;
        margin-top: 10%;
        justify-content: center;
    }
    .logoNotion {
        padding: 10px;
        height: 10%;
        width: 10%;
    }
    .logoSocialGit {
        height: 1%;
        width: 1%;
        padding-bottom: 3px;
    }
    .logoSocialLinkedin {
        height: 19%;
        width: 19%;
        padding-bottom: 10px;
    }
    .logoMail {
        height: 20%;
        width: 20%;
        padding-bottom: 8px;
    }
    @keyframes apparition {
        from {
            opacity: 0;
            transform: translateY(-400px);
        }
        to {
            opacity: 1;
            transform: translateY(0px);
        }
    }
}

