@media screen and (min-width:1024px) {
    body {
        background: #FAFFFC;
        background: -moz-linear-gradient(top, #FAFFFC 0%, #A45D42 100%, #A35B5B 100%);
        background: -webkit-linear-gradient(top, #FAFFFC 0%, #A45D42 100%, #A35B5B 100%);
        background: linear-gradient(to bottom, #FAFFFC 0%, #A35B5B 100%, #A35B5B 100%);
        min-height: 98vh;
    }
    #headBanner {
        background-color: #A35B5B;
        width: 100%;
        height: 10em;
        margin-left: -0.5em;
        margin-top: -0.5em;
        margin-bottom: 1rem;
        padding-right: 1.1em;
        display: inline-flex;
        justify-content: space-around;
        align-items: center;
    }
    #logo {
        height: 5em;
        padding-left: 4em;
        margin-right: 2em;
    }
    #title {
        font-size: 3em;
        color: white;
        padding-right: 2em;
        text-align: center;
        font-family: "Ubuntu", "Roboto", "Open Sans", sans-serif;
    }
    .informations {
        font-family: "Ubuntu", "Roboto", "Open Sans", sans-serif;
        background-color: white;
        display: inline-flex;
        flex-direction: column;
        margin-top: 50px;
        border-radius: 20rem;
        height: 37em;
        width: 100%;
        background: #FAFFFC;
        background: -moz-linear-gradient(top, #FAFFFC 0%, #A45D42 100%, #A35B5B 100%);
        background: -webkit-linear-gradient(top, #FAFFFC 0%, #A45D42 100%, #A35B5B 100%);
        background: linear-gradient(to bottom, #FAFFFC 0%, #A35B5B 100%, #A35B5B 100%);
    }
    #body-fiche {
        display: inline-flex;
        justify-content: space-around;
        align-items: center;
        width: 100%;
    }
    .bodyWilder {
        width: 100%;
        text-align: center;
    }
    .socialMedia {
        width: 100%;
        display: inline-flex;
        align-items: center;
        margin-top: 6rem;
        text-align: center;
    }
    .logoSocial {
        padding: 10px;
        height: 30%;
        width: 30%;
        animation-name: rotate;
        animation-duration: 2s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        animation-timing-function: ease-in-out;
    }
    .logoSocialGit {
        height: 50%;
        width: 50%;
        margin-left: 5em;
        transform: rotateY(2turn);
        animation-name: rotate;
        animation-duration: 2s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        animation-timing-function: ease-in-out;
    }
    .logoSocialLinkedin {
        height: 40%;
        width: 40%;
        padding-bottom: 5px;
        margin-left: 3em;
        animation-name: rotate;
        animation-duration: 2s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        animation-timing-function: ease-in-out;
    }
    .logoMail {
        height: 40%;
        width: 40%;
        padding-bottom: 8px;
        margin-right: 3em;
        animation-name: rotate;
        animation-duration: 2s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        animation-timing-function: ease-in-out;
    }
    @keyframes rotate {
        from {
            transform: rotateY(0deg);
        }
        to {
            transform: rotateY(360deg);
        }
    }
    .ficheWilder {
        width: 100%;
        height: 100%;
        margin-top: 2rem;
        justify-content: center;
    }
    .photoWilder {
        width: 70%;
        height: 70%;
        border-radius: 50%;
        animation-name: slideIn;
        animation-duration: 6s;
        animation-iteration-count: 1;
    }
    @keyframes slideIn {
        0% {
            transform: translate(0%, 0%);
        }
        50% {
            transform: translate(43%, 0%);
        }
        100% {
            transform: translate(0%, 0%);
        }
    }
    h2 {
        font-size: 2em;
        text-align: center;
        margin-top: 1em;
    }
    button {
        display: none;
    }
}
